import Guitar from '../model/Guitar';
import guitars from '../data/guitars.json';
import { Range } from '../model/GuitarFilters';

export default class GuitarService {
  private readonly guitarsCached: Guitar[] = [];

  getGuitars(): Guitar[] {
    if (this.guitarsCached.length === 0) {
      this.guitarsCached.push(...guitars);
    }

    return this.guitarsCached;
  }

  getCategories(): string[] {
    const all = this.getGuitars().map((guitar) => guitar.category);
    const distinct = all.filter((element, index) => index === all.indexOf(element));
    return distinct;
  }

  getBrands(): string[] {
    const all = this.getGuitars().map((guitar) => guitar.brand);
    const distinct = all.filter((element, index) => index === all.indexOf(element));
    return distinct;
  }

  getColors(): string[] {
    const all = this.getGuitars().map((guitar) => guitar.color);
    const distinct = all.filter((element, index) => index === all.indexOf(element));
    return distinct;
  }

  getPriceRange(): Range {
    const allGuitars = this.getGuitars();

    let maxPrice = 0;

    allGuitars.forEach((guitar) => {
      if (guitar.price > maxPrice) {
        maxPrice = guitar.price;
      }
    });

    let minPrice = maxPrice;

    allGuitars.forEach((guitar) => {
      if (guitar.price < minPrice) {
        minPrice = guitar.price;
      }
    });

    return {
      from: minPrice,
      to: maxPrice,
    };
  }
}

export const guitarService: GuitarService = new GuitarService();
