import { Dispatch, ReactNode, SetStateAction, useCallback, useEffect } from 'react';
import FilterContext from '../../../context/FilterContext';
import GuitarFilters from '../../../model/GuitarFilters';
import { saveJsonToLocalStorage } from '../../../utils/local-storage-utils';

interface FilterContextProviderProps {
  filters: GuitarFilters;
  setFilters: Dispatch<SetStateAction<GuitarFilters>>;
  children: ReactNode;
}

export default function FilterContextProvider({
  filters,
  setFilters,
  children,
}: FilterContextProviderProps) {
  const saveFiltersToLocalStorage = useCallback((filtersToSave: GuitarFilters): void => {
    saveJsonToLocalStorage<GuitarFilters>(filtersToSave, 'filters');
  }, []);

  useEffect(() => {
    saveFiltersToLocalStorage(filters);
  }, [filters, saveFiltersToLocalStorage]);

  return (
    <FilterContext.Provider value={{ filters, setFilters }}>{children}</FilterContext.Provider>
  );
}
