import { Dispatch, ReactNode, SetStateAction, useCallback, useEffect } from 'react';
import SortingContext from '../../../context/SortingContext';
import GuitarSorting from '../../../model/GuitarSorting';
import { saveJsonToLocalStorage } from '../../../utils/local-storage-utils';
import { Nullable } from '../../../utils/Nullable';

interface SortingContextProviderProps {
  sorting: Nullable<GuitarSorting>;
  setSorting: Dispatch<SetStateAction<Nullable<GuitarSorting>>>;
  children: ReactNode;
}

export default function SortingContextProvider({
  sorting,
  setSorting,
  children,
}: SortingContextProviderProps) {
  const saveSortingToLocalStorage = useCallback((sortingToSave: Nullable<GuitarSorting>): void => {
    saveJsonToLocalStorage<Nullable<GuitarSorting>>(sortingToSave, 'sorting');
  }, []);

  useEffect(() => {
    saveSortingToLocalStorage(sorting);
  }, [sorting, saveSortingToLocalStorage]);

  return (
    <SortingContext.Provider value={{ sorting, setSorting }}>{children}</SortingContext.Provider>
  );
}
