import { MouseEvent, useCallback, useContext, useMemo } from 'react';
import FilterContext, { FiltersState } from '../../context/FilterContext';
import ShoppingCartContext, { ShoppingCartState } from '../../context/ShoppingCartContext';
import SortingContext, { SortingState } from '../../context/SortingContext';
import GuitarFilters, { defaultGutarFilters } from '../../model/GuitarFilters';

export default function ResetButton() {
  const { filters, setFilters } = useContext(FilterContext) as FiltersState;
  const { sorting, setSorting } = useContext(SortingContext) as SortingState;
  const { shoppingCart, dispatchShoppingCartAction } = useContext(
    ShoppingCartContext
  ) as ShoppingCartState;

  const clearFilters = useCallback(() => {
    setFilters(() => {
      const defaultFilters = structuredClone(defaultGutarFilters) as GuitarFilters;
      return defaultFilters;
    });
  }, [setFilters]);

  const clearSorting = useCallback(() => {
    setSorting(() => {
      const defaultSorting = null;
      return defaultSorting;
    });
  }, [setSorting]);

  const clearShoppingCart = useCallback(() => {
    dispatchShoppingCartAction({ type: 'clearItems' });
  }, [dispatchShoppingCartAction]);

  const clearAll = useCallback(() => {
    clearFilters();
    clearSorting();
    clearShoppingCart();
  }, [clearFilters, clearShoppingCart, clearSorting]);

  const resetOptions = useMemo(
    () => [
      { id: 'clear-all', text: 'All', clickEventHandler: clearAll },
      { id: 'clear-filters', text: 'Filters only', clickEventHandler: clearFilters },
      { id: 'clear-sorting', text: 'Sorting only', clickEventHandler: clearSorting },
      { id: 'clear-cart', text: 'Cart only', clickEventHandler: clearShoppingCart },
    ],
    [clearAll, clearFilters, clearShoppingCart, clearSorting]
  );

  const handleClick = useCallback(
    (event: MouseEvent) => {
      const clickedButton = event.target as HTMLButtonElement;

      if (clickedButton.id) {
        resetOptions.find((option) => option.id === clickedButton.id)?.clickEventHandler();
      }
    },
    [resetOptions]
  );

  return (
    <div className="dropdown my-3 align-self-center">
      <button
        className="btn btn-danger dropdown-toggle"
        type="button"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        Clear selection
      </button>
      <ul className="dropdown-menu">
        {resetOptions.map((option) => (
          <li key={option.id}>
            <button id={option.id} className="dropdown-item" type="button" onClick={handleClick}>
              {option.text}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}
