import { ChangeEvent } from 'react';
import { CheckedChangedEvent } from '../../model/CheckedChangedEvent';
import { EventCallback } from '../../model/EventCallback';
import toKebabCase from '../../utils/kebab-case-translator';
import toSentenceCase from '../../utils/sentence-case-translator';

type FilterOptionProps = {
  value: string;
  checked: boolean;
  onCheckedChanged: EventCallback<CheckedChangedEvent>;
};

function FilterOption({ value, checked, onCheckedChanged }: FilterOptionProps) {
  function handleChange(event: ChangeEvent<HTMLInputElement>): void {
    const checkbox = event.target as HTMLInputElement;
    onCheckedChanged({ frontValue: value, checked: checkbox.checked });
  }

  return (
    <div className="form-check">
      <label className="form-check-label" htmlFor={toKebabCase(value)}>
        <input
          className="form-check-input"
          type="checkbox"
          id={toKebabCase(value)}
          checked={checked}
          onChange={handleChange}
        />
        {toSentenceCase(value)}
      </label>
    </div>
  );
}

export default FilterOption;
