/* eslint-disable react/jsx-no-bind */
import { useContext, useEffect, useState } from 'react';
import FilterContext, { FiltersState } from '../../context/FilterContext';
import GuitarFilters, { Range } from '../../model/GuitarFilters';
import { RangeChangedEvent } from '../../model/RangeChangedEvent';
import toKebabCase from '../../utils/kebab-case-translator';
import toSentenceCase from '../../utils/sentence-case-translator';
import RangeSlider from './RangeSlider';

type RangeFilterProps = {
  filterBy: string;
  min: number;
  max: number;
};

export default function RangeFilter({ filterBy, min, max }: RangeFilterProps) {
  const [show, setShow] = useState(false);
  const { filters, setFilters } = useContext(FilterContext) as FiltersState;

  let valueStart = (filters[filterBy] as Range).from;
  let valueEnd = (filters[filterBy] as Range).to;

  function handleShowChanged() {
    setShow(!show);
  }

  function handleRangeChanged({ from, to }: RangeChangedEvent): void {
    setFilters((prevFilters) => {
      const newFilters = structuredClone(prevFilters) as GuitarFilters;
      newFilters[filterBy] = { from, to };
      return newFilters;
    });
  }

  useEffect(() => {
    valueStart = (filters[filterBy] as Range).from;
    valueEnd = (filters[filterBy] as Range).to;
  }, [filters]);

  return (
    <div className="accordion-item">
      <h2 className="accordion-header" id={`filter-by-${toKebabCase(filterBy)}`}>
        <button
          className="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target={`#filter-${toKebabCase(filterBy)}`}
          aria-expanded="false"
          aria-controls={`filter-${toKebabCase(filterBy)}`}
          onClick={handleShowChanged}
        >
          {toSentenceCase(filterBy)}
        </button>
      </h2>
      <div
        id={`filter-${toKebabCase(filterBy)}`}
        className="accordion-collapse collapse"
        aria-labelledby={`filter-by-${toKebabCase(filterBy)}`}
        data-bs-parent="#filters"
      >
        <div className="accordion-body">
          <RangeSlider
            show={show}
            min={Math.floor(min)}
            max={Math.ceil(max)}
            valueStart={valueStart}
            valueEnd={valueEnd}
            onRangeChanged={handleRangeChanged}
          />
        </div>
      </div>
    </div>
  );
}
