import './App.scss';
import { useReducer, useState } from 'react';
import Layout from '../Layout/Layout';
import GuitarContext from '../../context/GuitarContext';
import { guitarService } from '../../services/GuitarService';
import GuitarFilters, { defaultGutarFilters } from '../../model/GuitarFilters';
import GuitarSorting from '../../model/GuitarSorting';
import { Nullable } from '../../utils/Nullable';
import { parseJsonFromLocalStorage } from '../../utils/local-storage-utils';
import FilterContextProvider from './ContextProviders/FilterContextProvider';
import SortingContextProvider from './ContextProviders/SortingContextProvider';
import ShoppingCartContextProvider from './ContextProviders/ShoppingCartContextProvider';
import { shoppingCartReducer } from '../../services/ShoppingCartReducer';
import ShoppingCart from '../../model/ShoppingCart';

function initFilters(): GuitarFilters {
  return parseJsonFromLocalStorage<GuitarFilters>(
    'filters',
    structuredClone(defaultGutarFilters) as GuitarFilters
  );
}

function initSorting(): Nullable<GuitarSorting> {
  return parseJsonFromLocalStorage<Nullable<GuitarSorting>>('sorting', null);
}

function initShoppingCart(): ShoppingCart {
  return parseJsonFromLocalStorage<ShoppingCart>('shoppingCart', {
    guitars: [],
    pendingOverflow: false,
  });
}

export default function App() {
  const [filters, setFilters] = useState(initFilters());
  const [sorting, setSorting] = useState(initSorting());
  const [shoppingCart, dispatchShoppingCartAction] = useReducer(
    shoppingCartReducer,
    initShoppingCart()
  );

  return (
    <GuitarContext.Provider value={guitarService}>
      <FilterContextProvider filters={filters} setFilters={setFilters}>
        <SortingContextProvider sorting={sorting} setSorting={setSorting}>
          <ShoppingCartContextProvider
            shoppingCart={shoppingCart}
            dispatchShoppingCartAction={dispatchShoppingCartAction}
          >
            <Layout />
          </ShoppingCartContextProvider>
        </SortingContextProvider>
      </FilterContextProvider>
    </GuitarContext.Provider>
  );
}
