export type Range = {
  from: number;
  to: number;
};

export default interface GuitarFilters {
  [key: string]: unknown;

  category: string[];
  brand: string[];
  color: string[];
  price: Range;
  inStock: boolean[];
  searchQuery: string;
}

export const defaultGutarFilters: GuitarFilters = {
  category: [],
  brand: [],
  color: [],
  price: {
    from: 0,
    to: 999999,
  },
  inStock: [],
  searchQuery: '',
};
