import { useContext } from 'react';
import GuitarService from '../../services/GuitarService';
import GuitarCard from '../GuitarCard/GuitarCard';
import GuitarContext from '../../context/GuitarContext';
import FilterContext, { FiltersState } from '../../context/FilterContext';
import { Range } from '../../model/GuitarFilters';
import SortingContext, { SortingState } from '../../context/SortingContext';
import sortingMapper from '../../model/SortingMapper';

function GuitarCardList() {
  const guitarService: GuitarService | undefined = useContext(GuitarContext);
  const { filters } = useContext(FilterContext) as FiltersState;
  const { sorting } = useContext(SortingContext) as SortingState;

  const guitars = guitarService?.getGuitars() || [];

  // Apply filters
  let filteredGuitars = guitars;

  Object.keys(filters).forEach((filter) => {
    if (Object.prototype.hasOwnProperty.call(filters, filter)) {
      const filterContent = filters[filter];

      // Apply textual filters,
      // formulated as string arrays)
      if (Array.isArray(filterContent)) {
        if (filterContent.length > 0) {
          filteredGuitars = filteredGuitars.filter((guitar) =>
            filterContent.includes(guitar[filter])
          );
        }
      }

      // Apply number range filters,
      // formulated as objects with from and to properties
      if (
        Object.prototype.hasOwnProperty.call(filterContent, 'from') &&
        Object.prototype.hasOwnProperty.call(filterContent, 'to')
      ) {
        filteredGuitars = filteredGuitars.filter((guitar) => {
          const guitarParam = guitar[filter] as number;
          const filterFrom = (filterContent as Range).from;
          const filterTo = (filterContent as Range).to;
          return guitarParam >= filterFrom && guitarParam <= filterTo;
        });
      }

      // Apply search query
      if (filter === 'searchQuery') {
        filteredGuitars = filteredGuitars.filter((guitar) => {
          const query = (filterContent as string).toLowerCase();

          if (query.length === 0) {
            return true;
          }

          return guitar.model.toLowerCase().includes(query);
        });
      }
    }
  });

  // Apply sorting
  const sortedGuitars = filteredGuitars;

  if (sorting) {
    const compareFn = sortingMapper.find(
      (mapping) => mapping.property === sorting.property && mapping.order === sorting.order
    )?.compareFn;

    if (compareFn) {
      sortedGuitars.sort(compareFn);
    }
  }

  return (
    <div className="col gap-3 d-flex flex-row flex-wrap justify-content-center">
      {guitars.length === 0 && <p>Loading guitars...</p>}
      {sortedGuitars.length === 0 && <p>No guitars found matching the selected criteria.</p>}
      {sortedGuitars.map((guitar) => (
        <GuitarCard guitar={guitar} key={guitar.model} />
      ))}
    </div>
  );
}

export default GuitarCardList;
