import { Dispatch, ReactNode, useCallback, useEffect } from 'react';
import ShoppingCartContext from '../../../context/ShoppingCartContext';
import Guitar from '../../../model/Guitar';
import ShoppingCart from '../../../model/ShoppingCart';
import { ShoppingCartAction } from '../../../services/ShoppingCartReducer';
import { saveJsonToLocalStorage } from '../../../utils/local-storage-utils';

interface ShoppingCartContextProviderProps {
  shoppingCart: ShoppingCart;
  dispatchShoppingCartAction: Dispatch<ShoppingCartAction>;
  children: ReactNode;
}

export default function ShoppingCartContextProvider({
  shoppingCart,
  dispatchShoppingCartAction,
  children,
}: ShoppingCartContextProviderProps) {
  const saveShoppingCartToLocalStorage = useCallback((shoppingCartToSave: ShoppingCart): void => {
    saveJsonToLocalStorage<ShoppingCart>(shoppingCartToSave, 'shoppingCart');
  }, []);

  useEffect(() => {
    saveShoppingCartToLocalStorage(shoppingCart);
  }, [saveShoppingCartToLocalStorage, shoppingCart]);

  return (
    <ShoppingCartContext.Provider value={{ shoppingCart, dispatchShoppingCartAction }}>
      {children}
    </ShoppingCartContext.Provider>
  );
}
