import { useContext } from 'react';
import ShoppingCartContext, { ShoppingCartState } from '../../context/ShoppingCartContext';
import CartItemsCounter from './CartItemsCounter';

export default function ShoppingCart() {
  const { shoppingCart, dispatchShoppingCartAction } = useContext(
    ShoppingCartContext
  ) as ShoppingCartState;

  return (
    <i className="bi bi-cart4 position-relative">
      {shoppingCart.guitars.length > 0 && <CartItemsCounter count={shoppingCart.guitars.length} />}
    </i>
  );
}
